.special__wrapper {
  padding: 31px 30px 36px;
}

.special__title {
  margin-bottom: 5px;
}

.special__tag {
  color: $accent-color;
  text-transform: uppercase;
  font-size: 17px;
  line-height: 20px;
  font-weight: 700;
  margin: 0 0 17px;
}

.special__description {
  margin: 0 0 28px 0;
}

.special__table {
  margin-bottom: 41px;
  width: 100%;
}

.special__image {
  width: 100%;
  max-width: 320px;
  display: block;
  margin: 0 auto 25px;
}

.special__price {
  font-weight: 700;
  font-size: 17px;
  text-align: center;
  margin: 0 0 25px 0;
}

.special__button {
  width: 100%;
}

@media (min-width: $tablet-width) {
  .special__wrapper {
    padding: 40px 50px 52px;
  }

  .special__header {
    display: flex;
    justify-content: space-between;
    margin-bottom: 35px;
  }

  .special__tag {
    align-self: center;
    width: 100px;
    text-align: center;
  }

  .special__main {
    width: 50%;
    padding-right: 42px;
    box-sizing: border-box;
    position: relative;
  }

  .special__description {
    margin-bottom: 48px;
    font-size: 17px;
    line-height: 30px;
  }

  .special__image {
    width: 100%;
    height: auto;
    position: absolute;
    right: -100%;
    bottom: 0;
    margin: 0;
    max-width: 334px;
  }

  .special__table {
    margin-bottom: 57px;
  }

  .special__price {
    font-size: 20px;
    margin-bottom: 33px;
  }
}

@media (min-width: $desktop-width) {
  .special__wrapper {
    padding: 115px 50px 73px;
    max-width: 1050px;
    margin: 0 auto;
  }

  .special__header {
    flex-direction: row-reverse;
    justify-content: start;
  }

  .special__title {
    z-index: 10;
  }

  .special__tag {
    margin: 0 43px 0 32px;
  }

  .special__main {
    width: 292px;
    margin-left: 175px;
    padding-right: 0;
  }

  .special__image {
    width: 525px;
    height: 636px;
    right: -200%;
    max-width: none;
  }

  .special__description {
    margin-bottom: 60px;
  }

  .special__table {
    margin-bottom: 110px;
  }
}
